:root {
  --primaryColor: #f1f1f1;
  --secondaryColor: #2b1f4d;
  --blanco: #ffffff;
  --black: #222;
  --gris1: #edf2f6;
  --gris2: #2f3848;
  --gris3: #1a202d;
  --negro: #000000;
  --celeste:#0089d0;
  --red:#eb0028;

  --headingFont:'Raleway', sans-serif;
  --textFont:  'Roboto', sans-serif;
}


/*
======================
Configuraciones Generales
======================
*/


body {
  font-family: 'Manrope', sans-serif;
  background-color: #fff;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1 {
  text-align: center;
}
h2 {
  font-size: 30px;
}
h4 {
  font-size: 16px;
  font-weight: 700;
}

button {
  border-style: none;
}

header {
  position: fixed;
  top: 0;
  z-index: 300;
  width: 100%;
  padding: 10px 0 10px 0;
  background-color: var(--blanco);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}



a {
  text-decoration: none;
  font-family: var(--textFont);
}


.container {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 16px;
}

/*
======================
Utilidades
======================
*/

.row {
  display: flex;
  justify-content: space-between;

}



/*
======================
Navegacion
======================
*/


.navegacion {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 20px;
}

.nav__icons {
  display:flex;
  justify-content: space-between;
  align-items: center;
}

.nav__icons .usuario {
  margin-right: 20px;
  transition: all .3s ease-in-out;
}

.nav__icons #nombre:hover{
  cursor: pointer;
  color: var(--red);
}

.nav__icons a.icon {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  background-color: var(--blanco);
  width: 32px;
  height: 32px;
  border: 1px solid var(--black);
  border-radius: 50%;
  color: var(--black);
  margin-right: 10px;
  transition: all .5s ease;
}

.nav__icons a.icon:hover{
  background-color: var(--red);
}

.nav__icons a.admin:hover{
  background-color: #F9F871;
}

.logo {
  font-family: 'Kalam', cursive;
  font-weight: 700;
  font-size: 30px;
  color: #222222;
  line-height: 50px;
  
}

@media (max-width: 540px ) {
  .logo {
    font-size: 40px;
  }

  .navegacion {
    display:block;
    text-align: center;
  }
  .icono {
    display:block;
  }
  .nav__icons {
    justify-content:center;
  }
  .usuario {
    display:block;
    color:black;
  }
}


/*
======================
Efecto Logo
======================
*/

.efecto {
  display: inline-block;
  position: relative;
  padding: 5px 10px;
  overflow: hidden;
  transition: 0.2s;
  border: none;
  cursor: pointer;
}

.efecto:hover{
  background-color: #a945c7;
  box-shadow: 0 0 10px #a945c7, 
              0 0 40px  #a945c7,
              0 0 80px  #a945c7;
  transition-delay: 1s;
}

.efecto span {
  position: absolute;
  display: block;    
}

#span1{
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent,#a945c7);
}

.efecto:hover #span1{
  left: 100%;
  transition:1s;
}

#span3{
  bottom: 0;
  right: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, transparent,#a945c7);
}

.efecto:hover #span3{
  right: 100%;
  transition:1s;
  transition-delay: 0.5s;
}

#span2{
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, transparent,#a945c7);
}

.efecto:hover #span2{
  top: 100%;
  transition:1s;
  transition-delay: 0.25s;
}

#span4{
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(360deg, transparent,#a945c7);
}

.efecto:hover #span4{
  bottom: 100%;
  transition:1s;
  transition-delay: 0.75s;
}

.submenu {
  position: relative;
  top:0px;
}

.submenu i {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  background-color: var(--blanco);
  width: 32px;
  height: 32px;
  border: 1px solid var(--black);
  border-radius: 50%;
  color: var(--black);  

}


.submenu #carrito{
  display: none;
  overflow-y: scroll;
}


@media (max-width: 758px ) {
  
  .submenu #carrito{
    border: 1px solid var(--gris2);
    -webkit-box-shadow: 0px 6px 11px -8px rgba(0,0,0,1);
    -moz-box-shadow: 0px 6px 11px -8px rgba(0,0,0,1);
    box-shadow: 0px 6px 11px -8px rgba(0,0,0,1);
    margin-right: 10px;
    border-radius: 5px;
  
  }
  

}


@media (max-width: 480px ) {
  
  .submenu #carrito{
    max-width: 100px;
    overflow: scroll;
  
  }
  

}


.notificacion p {
  background-color:#CC6666;
  color: var(--blanco);
  width: 20px;
  height: 20px;
  border-radius: 50%;
  text-align: center;
  position: absolute;
  top: -10px;
  left: 20px;
  line-height: 20px;
 
  
}

button.btn-eliminar{
  background:none;
}

.carrito{
  width: 100%;
}

.carrito img {
  padding-top: 10px;
  width: 70px;
  height: auto;
  object-fit: cover;
}

.carrito thead th{
  padding-bottom: 7px;
  padding-right: 25px;
  border-bottom: 1px solid #888;
  font-size: 14px;
}
.carrito tbody td{
  padding-right: 20px;
  text-align: center;
  font-size: 10px;
}

.total {
  display: flex;
  justify-content: flex-end;
  font-size: 20px;
}

.total p {
  margin-bottom: 10px;
  margin-right: 45px;
  margin-top: 15px;
  text-transform: uppercase;
}

.total p span {
  font-size: 25px;
  font-weight: 700;
  color: red;
}




.btn-vaciar {
  width: 100%;
  display: block;
  height: 38px;
  padding: 0 30px;
  color: #555;
  text-align: center;
  font-size: 11px;
  font-weight: 600;
  line-height: 38px;
  letter-spacing: 1px;
  text-transform: uppercase;
  text-decoration: none;
  white-space: nowrap;
  background-color: transparent;
  border-radius: 4px;
  border: 1px solid #bbb;
  cursor: pointer;
  box-sizing: border-box;
}

.btn-pagar {
  width: 100%;
  display: block;
  height: 38px;
  padding: 0 30px;
  color: #555;
  text-align: center;
  font-size: 11px;
  font-weight: 600;
  line-height: 38px;
  letter-spacing: 1px;
  text-transform: uppercase;
  text-decoration: none;
  white-space: nowrap;
  background-color: transparent;
  border-radius: 4px;
  border: 1px solid #bbb;
  cursor: pointer;
  box-sizing: border-box;
  margin-top: 20px;
}



.btn-pagar,
.btn-vaciar:hover {
  color:red;
  border-color: #888;
  outline: 0;
}

.submenu i {
  transition: all .5s ease;
}

.submenu i:hover {
  background-color: var(--red);
}

.submenu:hover #carrito{
  display: block;
  position: absolute;
  right:0;
  top:100%;
  z-index: 1;
  background-color: white;
  padding: 20px;
  min-height: 330px;
  max-height: 400px;
  min-width: 300px;
}


.vacio {
  padding: 10px;
  background-color: crimson;
  text-align: center;
  border-radius: 10px;
  color: white;
}





/*
======================
Carousel
======================
*/
.margin-80 {
  display: block;
  margin-top: 80px!important;
}

.carousel-item img {
  height: 770px;
  object-fit: cover;
 
}


/*
======================
Hero Area
======================
*/

.hero,
.hero .glide__slides {
  background-color: var(--primaryColor);
  position: relative;
  width: 100%;
  height: 100vh;
}

.hero .glide__bullet {
  background-color: #222;
  width: 12px;
  height: 12px;
}

.hero .glide__arrow {
  padding: 15px 17px;
  opacity: 0;
  border: none;
  background-color: var(--grey);
  transition: all 0.5s ease-in-out 0.2s;
}

.glide__arrow:hover {
  background-color: var(--black);
}

.glide__arrow--left {
  left: 200px;
}

.glide__arrow--right {
  position: absolute;
  right: 200px;
}

.hero:hover .glide__arrow {
  opacity: 1;
}

.hero:hover .glide__arrow--left {
  left: 230px;
}

.hero:hover .glide__arrow--right {
  right: 230px;
}

.hero .glide__arrow svg {
  height: 18px;
  width: 18px;
  fill: var(--primaryColor);
}

.hero .glide__arrow {
  border-radius: 50%;
}

.hero__center {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  max-width: 1140px;
  margin: 0 auto;
  height: 100%;
  padding-top: 30px;
}

.hero__left {
  padding: 0 30px;
  flex: 0 0 50%;
}

.hero__btn {
  display: inline-block;
  font-weight: 700;
  font-size: 14px;
  background-color: var(--black);
  color: var(--primaryColor);
  cursor: pointer;
  margin-top: 10px;
  padding: 15px 40px;
  border: 1px solid var(--black);
  transition: all 0.3s ease-in-out;
}

.hero__btn:focus {
  outline: none;
}

.hero__left .hero__btn:hover {
  font-weight: 700;
  background-color: transparent;
  color: var(--black);
}

.hero__left h1 {
  margin: 10px 0;
  text-align: left;
}

.hero__left p {
  margin-bottom: 10px;
}

.hero__right {
  flex: 0 0 50%;
  position: relative;
  text-align: center;
}

.hero__right img.banner_03 {
  width: 70%;
}

/*
======================
Hero Media Query
======================
*/
@media only screen and (max-width: 999px) {
  .hero__center {
    flex-direction: column;
    text-align: center;
  }

  .hero__left h1 {
    text-align: center;
  }
  

  .hero__right {
    top: 50%;
    position: absolute;
  }

  .hero__left {
    position: absolute;
    padding: 0 10px;
    top: 20%;
  }

  .hero__right img {
    width: 55%;
  }

  .hero__btn {
    padding: 11px 28px;
  }

  .hero .glide__arrows {
    display: none;
  }
}

@media only screen and (max-width: 567px) {
  .hero,
  .hero .glide__slides {
    height: 60vh;
  }

  .hero__right {
    display: none;
  }
}


/*
======================
Collection 
======================
*/

.section {
  padding: 30px 0;
}

.collection {
  margin: 30px auto;
}

.collection__container {
  width: 100%;
  padding: 0 15px;
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: space-between;
}

.collection__box {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 10px;
  flex: 0 0 48%;
  height: 300px;
  background-color: var(--primaryColor);
}

.collection__box:not(:last-child) {
  margin-right: 16px;
}

.img__container {
  width: 250px;
  text-align: center;
}

.collection__box img.collection_01 {
  width: 60%;
}

.collection__box img.collection_02 {
  width: 55%;
}

.collection__content {
  flex: 0 0 50%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.collection__content span {
  color: var(--black);
}

.collection__content h1 {
  margin-top: 5px;
  font-size:24px;
}

.collection__content a:link,
.collection__content a:visited {
  font-weight: 700;
  display: inline-block;
  padding: 12px;
  margin-top: 13px;
  border-radius: 30px;
  border: 2px solid var(--secondaryColor);
  color: var(--primaryColor);
  background-color: var(--secondaryColor);
  transition: all 0.3s ease-in-out;
  font-size: 14px;
}

.collection__content a:hover {
  background-color: transparent;
  color: var(--secondaryColor);
}

/*
======================
Collection Media Query
======================
*/
@media only screen and (max-width: 999px) {
  .collection__container {
    width: 80%;
    margin: 0 auto;
    flex-direction: column;
    height: 650px;
  }

  .collection__box {
    width: 100%;
    margin: 0 auto;
  }

  .collection__box:not(:last-child) {
    margin: 0 0 16px;
  }
}

@media only screen and (max-width: 568px) {
  .collection {
    margin: 0;
    position: relative;
  }

  .collection__container {
    width: 100%;
    height: 500px;
    text-align: center;
    flex-direction: column;
    justify-content: space-around;
  }

  .collection__box {
    justify-content: space-around;
    height: 150px;
  }

  .collection__content {
    flex: 0 0 30%;
  }

  .collection__data span {
    font-size: 12px;
  }

  .collection__data h1 {
    font-size: 20px;
  }
}











#encabezado {
  margin: 30px 0;
  font-size: 40px;
  padding: 20px;
  /* border-bottom: 1px solid black; */
  background-color: var(--primaryColor);
}

#lista-cursos .row {
  margin-bottom:20px;
}

.contenedor-grid {
  
  display: grid!important;
  grid-template-columns: repeat(4, 1fr)!important;
  gap: 20px;
  overflow: hidden;
}


@media (max-width: 800px ) {
  .contenedor-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr)!important;
  }
}



@media (max-width: 600px ) {
  .contenedor-grid {
    grid-template-columns: repeat(2, 1fr)!important;    
  }
}
@media (max-width: 420px ) {
  .contenedor-grid {
    display: grid;
    grid-template-columns: repeat(1, 1fr)!important;
    
  }

  .card {
    width: 300px!important;
    overflow: hidden;

  }
}



.card {
  text-align: center;
  border: 1px solid #e1e1e1;
  background-color:#f5f3f3;
  transition: all .3s ease;
  border-radius: 7px;
  width: 88%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}



.card:hover {
  background-color: #fff;
  -webkit-box-shadow: 0px 6px 11px -8px rgba(0,0,0,1);
  -moz-box-shadow: 0px 6px 11px -8px rgba(0,0,0,1);
  box-shadow: 0px 6px 11px -8px rgba(0,0,0,1);

}



.imagen-producto {
  display: block;
  width: 70%;
  height: 100%;
  object-fit: cover;
  margin: 0px auto;
  padding:20px 10px 10px 10px;
  
}


.info-card  {
  padding: 10px 20px;
  padding-bottom: 15px;
  text-align: center;
}

.info-card p, 
.card h4 {
  margin-bottom: 5px;
}
.info-card .precio {

  font-size: 18px;
  margin-top: 10px;
  margin-bottom: 20px;
}


.agregar-carrito {
 
  display: inline-block;
  font-weight: 700;
  text-transform: uppercase;
  width: 100%;
  padding: 7px 3px;
  border: 1px solid var(--black);
  color: var(--black);
  cursor: pointer;
  transition: all .3s ease;
  font-size: 14px;
 
}

.agregar-carrito:hover {
  background-color: var(--black);
  color: var(--primaryColor);
}



/* Footer */

.footer {
  margin-top: 70px;
  padding: 60px 10px;
  border-top:1px solid #aaa;
  background: #252525;
}

.footer .footer-box .menu h3{
  font-size: 18px;
  color: #CCCCCC;
  margin: 0 0 10px 0;
}

.footer-box {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  
}

