.footer .footer-box .menu li {
  margin-bottom: 10px;
}


.footer .footer-box .menu a{
  font-size: 13px;
  text-decoration: none;
  color: #959595;
  transition: all .3s ease;
}

.footer .footer-box .menu i {
  margin-right: 10px;
  font-size: 17px;
}



.footer .footer-box .menu a:hover{
  color:var(--red);
}


