/*
======================
Modal Section
======================
*/

.flex-modal {
  display: flex;
  justify-content:space-between;
  gap:20px;
}

.titulo-modal {
  padding: 10px;
  text-align: center;
}

.imagen-modal {
  margin-top: 30px;
  width:200px;
  height:auto;
}

.btn-modal {
  display:block;
  margin: 20px auto 0 auto;
  background-color: var(--celeste);
  padding:10px 20px;
  border-radius: 5px;
  color:white;
  transition: all 0.4s ease;
}

.contenedor-cantidad i:hover,
.btn-modal:hover {
  background-color: rgba(0, 0, 255, 0.815);
}
.contenedor-cantidad p {
  margin-top: 15px;
}


.ancho-fijo {
  width:350px;
  text-align: justify;
}


.contenedor-cantidad {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 20px 0;
  font-size:16px;
}
.contenedor-cantidad i {
  flex: 0 0 40px;
  height: 42px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.4s ease;
}
.fa-minus,
.fa-plus {
  color: white;
}
.fa-minus:hover,
.fa-plus:hover {
  cursor: pointer;
}
.contenedor-cantidad .fa-plus,
.contenedor-cantidad .fa-minus  {
  background-color: var(--celeste);
}

.precio-modal {
  text-align: left;
  margin-left: 5px;
  font-size: 20px;
}

.total-modal{
  color: red;
  font-weight:bold;
  font-size:26px;
  margin: 10px 0;
}

#transition-modal-title{
  font-size: 32px;
  padding: 10px;
}

#transition-modal-description {
  font-size: 14px;
  text-align:justify;
} 


